<template>
    <v-container>
        <v-app-bar
                app
                color="#FFFFFF"
                elevation="3"
        >
            <v-row>
                <v-col class="text-left pa-0">
                    <v-btn text icon color="primary"
                           tile
                           height="100%"
                           width="50px"
                           @click="$router.go(-1)">

                        <v-icon>mdi-arrow-left</v-icon>
                    </v-btn>
                </v-col>
                <v-col class="text-center">
                    <v-toolbar-title>{{changeNameAndLastNameText}}</v-toolbar-title>
                </v-col>
                <v-col>
                </v-col>
            </v-row>
        </v-app-bar>

        <v-card class="pa-5" style="margin-top: 60px;">
            <v-form v-model="validForm">
                <v-text-field
                        v-model="name"
                        :rules="[rules.required]"
                        :label="nameText"
                        required
                ></v-text-field>

                <v-text-field
                        v-model="lastName"
                        :rules="[rules.required]"
                        :label="lastNameText"
                        required
                ></v-text-field>
            </v-form>
            <v-btn block color="primary" style="margin-top: 10px" @click="saveNameAndLastName" :disabled="buttonIsDisabled">
                {{saveText}}
            </v-btn>
        </v-card>

        <v-snackbar
                v-model="snackbar"
                :timeout="4000"
        >
            {{ snackbarText }}
            <v-btn
                    color="blue"
                    text
                    @click="snackbar = false"
            >
                {{closeText}}
            </v-btn>
        </v-snackbar>
    </v-container>
</template>

<script>
    import Constants from "../../store/Constants";
    import {mapActions} from 'vuex';

    export default {
        name: "SettingsAccountEditNameAndLastName",
        data() {
            return {
                buttonIsDisabled: false,
                validForm: false,
                snackbar: false,
                snackbarText: null,
                rules: {
                    required: value => !!value || Constants.RULE_TEXT_MANDATORY_FIELD,
                },
                closeText: Constants.TEXT_CLOSE,
                changeNameAndLastNameText: Constants.TEXT_CHANGE_NAME_AND_LASTNAME,
                nameText: Constants.TEXT_NAME,
                lastNameText: Constants.TEXT_LASTNAME,
                name: this.$store.getters['user/getName'],
                lastName: this.$store.getters['user/getLastName'],
                saveText: Constants.TEXT_SAVE

            }
        },
        methods: {
            ...mapActions('user', ['updateAccountNameAndLastName']),

            saveNameAndLastName: function () {
                let data = {};
                let infoToUpdate = false;

                if(this.name != this.$store.getters['user/getName']){
                    data = {
                        name: this.name,
                        ...data
                    }
                    infoToUpdate = true;
                }

                if(this.lastName != this.$store.getters['user/getLastName']){
                    data = {
                        lastName: this.lastName,
                        ...data
                    }
                    infoToUpdate = true;
                }

                if(infoToUpdate){
                    let resultUpdateAccountNameAndLastName = this.updateAccountNameAndLastName(data);

                    resultUpdateAccountNameAndLastName.then(() => {
                        this.$router.go(-1);
                    })
                        .catch((error) => {
                            this.snackbarText = error;
                            this.snackbar = true;
                        });
                }

            }
        }
    }
</script>

<style scoped>

</style>